<template>
  <div class="todo-app" :class="{ 'dark-mode': isDarkMode }">
    <!-- ÂÖ±‰∫´ÁªÑ‰ª∂ - Ê†áÈ¢òÂíå‰∏ªÈ¢òÂàáÊç¢ -->
    <header class="app-header">
      <h1 class="app-title">‰ªäÊó•„ÅÆ„Çø„Çπ„ÇØ</h1>
      <button class="theme-toggle" @click="toggleTheme">
        <span class="theme-icon">{{ isDarkMode ? '‚òÄÔ∏è' : 'üåô' }}</span>
      </button>
    </header>

    <!-- Ê∑ªÂä†‰ªªÂä°Âå∫Âüü -->
    <div class="task-input-container">
      <div class="input-group">
        <input type="text" class="task-input" v-model="tag" placeholder="„Çø„Ç∞" @keyup.enter="focusTodoInput">
        <input type="text" class="task-input task-input-main" v-model="todo" placeholder="„ÇÑ„Çâ„Å™„ÅÅ~ „ÅÇ„Åã„Çì„Åì„Å®" ref="todoInput"
          @keyup.enter="addTodo">
        <button class="add-btn" @click="addTodo">
          <span class="btn-icon">+</span>
          <span class="btn-text">ËøΩÂä†</span>
        </button>
      </div>
    </div>

    <!-- ÊêúÁ¥¢‰∏éËøáÊª§Âå∫Âüü -->
    <div class="search-container">
      <div class="search-wrapper">
        <span class="search-icon">üîç</span>
        <input type="text" class="search-input" v-model="searchQuery" placeholder="„Çø„Çπ„ÇØ„ÇíÊ§úÁ¥¢...">
        <button class="clear-search" v-if="searchQuery" @click="searchQuery = ''">√ó</button>
      </div>

      <div class="filter-options">
        <button class="filter-btn" :class="{ active: activeFilter === 'all' }" @click="setFilter('all')">
          „Åô„Åπ„Å¶
        </button>
        <button class="filter-btn" :class="{ active: activeFilter === 'active' }" @click="setFilter('active')">
          Êú™ÂÆå‰∫Ü
        </button>
        <button class="filter-btn" :class="{ active: activeFilter === 'completed' }" @click="setFilter('completed')">
          ÂÆå‰∫ÜÊ∏à„Åø
        </button>
      </div>
    </div>

    <!-- „Çø„Çπ„ÇØ‰∏ÄË¶ß -->
    <div class="tasks-container">
      <div class="tasks-header">
        <div class="task-sort-menu">
          <span class="sort-label">‰∏¶„Å≥Êõø„Åà:</span>
          <button class="sort-option" v-for="(label, key) in sortOptions" :key="key"
            :class="{ active: sortKey === key }" @click="sortBy(key)">
            {{ label }}
            <span class="sort-arrow" v-if="sortKey === key">
              {{ sortOrders[key] > 0 ? '‚Üë' : '‚Üì' }}
            </span>
          </button>
        </div>
      </div>

      <!-- „Çø„Çπ„ÇØ„É™„Çπ„Éà -->
      <transition-group name="task-list" tag="ul" class="task-list">
        <li v-for="todo in filteredTodos" :key="todo.id" class="task-item"
          :class="{ 'task-completed': todo.complete, 'task-selected': selectedId === todo.id }" @click="edit(todo.id)">

          <div class="task-checkbox-container" @click.stop>
            <input type="checkbox" :id="'task-' + todo.id" v-model="todo.complete" @change="completed(todo)"
              class="task-checkbox">
            <label :for="'task-' + todo.id" class="checkbox-label"></label>
          </div>

          <div class="task-content">
            <div class="task-tag" v-if="todo.tag">{{ todo.tag }}</div>
            <div class="task-text" :class="{ 'completed-text': todo.complete }">
              {{ todo.todo }}
            </div>
          </div>

          <div class="task-actions">
            <button class="task-action edit-btn" @click.stop="edit(todo.id)">
              <span class="action-icon">‚úé</span>
            </button>
            <button class="task-action delete-btn" @click.stop="deleteTodo(todo)">
              <span class="action-icon">√ó</span>
            </button>
          </div>
        </li>
      </transition-group>

      <!-- Á©∫Áä∂ÊÄÅÊòæÁ§∫ -->
      <div class="empty-state" v-if="filteredTodos.length === 0">
        <div class="empty-icon">üìù</div>
        <p class="empty-message">
          {{ searchQuery ? 'Ê§úÁ¥¢ÁµêÊûú„ÅØ„ÅÇ„Çä„Åæ„Åõ„Çì' : '„Çø„Çπ„ÇØ„Åå„ÅÇ„Çä„Åæ„Åõ„Çì„ÄÇÊñ∞„Åó„ÅÑ„Çø„Çπ„ÇØ„ÇíËøΩÂä†„Åó„Åæ„Åó„Çá„ÅÜÔºÅ' }}
        </p>
      </div>
    </div>

    <!-- Â∫ïÈÉ®Áä∂ÊÄÅÊ†è -->
    <footer class="app-footer">
      <span class="items-count">
        {{ activeTaskCount }} ÂÄã„ÅÆ„Çø„Çπ„ÇØ„ÅåÊÆã„Å£„Å¶„ÅÑ„Åæ„Åô
      </span>
      <button class="clear-completed-btn" v-if="completedTaskCount > 0" @click="clearCompleted">
        ÂÆå‰∫Ü„Åó„Åü„Çø„Çπ„ÇØ„Çí„ÇØ„É™„Ç¢
      </button>
    </footer>

    <!-- Á∑®ÈõÜ„É¢„Éº„ÉÄ„É´ -->
    <div class="modal-overlay" v-if="isEditing" @click="cancelEdit">
      <div class="edit-modal" @click.stop>
        <h3 class="modal-title">„Çø„Çπ„ÇØ„ÇíÁ∑®ÈõÜ</h3>
        <div class="modal-inputs">
          <input type="text" class="modal-input" v-model="editingTask.tag" placeholder="„Çø„Ç∞">
          <input type="text" class="modal-input" v-model="editingTask.todo" placeholder="„ÇÑ„Çâ„Å™„ÅÅ~ „ÅÇ„Åã„Çì„Åì„Å®"
            ref="editTaskInput">
        </div>
        <div class="modal-actions">
          <button class="modal-btn cancel-btn" @click="cancelEdit">„Ç≠„É£„É≥„Çª„É´</button>
          <button class="modal-btn save-btn" @click="saveEdit">‰øùÂ≠ò</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      todos: [],
      todo: '',
      tag: '',
      searchQuery: '',
      nextTodoId: 1,
      selectedId: null,
      columns: ['id', 'tag', 'todo', 'complete', 'delete'],
      sortKey: 'id',
      sortOrders: {
        id: 1,
        tag: 1,
        todo: 1,
        complete: 1
      },
      sortOptions: {
        id: 'ID',
        tag: '„Çø„Ç∞',
        todo: '„Çø„Çπ„ÇØÂêç',
        complete: 'ÂÆå‰∫ÜÁä∂ÊÖã'
      },
      activeFilter: 'all',
      isDarkMode: false,
      isEditing: false,
      editingTask: { id: null, tag: '', todo: '', complete: false }
    }
  },
  computed: {
    filteredTodos() {
      let result = this.todos;

      // ÂÖàÊåâÊêúÁ¥¢ËøáÊª§
      if (this.searchQuery) {
        const query = this.searchQuery.toLowerCase();
        result = result.filter(todo =>
          todo.todo.toLowerCase().includes(query) ||
          (todo.tag && todo.tag.toLowerCase().includes(query))
        );
      }

      // Ê†πÊçÆÁä∂ÊÄÅËøáÊª§
      if (this.activeFilter === 'active') {
        result = result.filter(todo => !todo.complete);
      } else if (this.activeFilter === 'completed') {
        result = result.filter(todo => todo.complete);
      }

      // ÊéíÂ∫è
      const order = this.sortOrders[this.sortKey];
      return result.sort((a, b) => {
        const aVal = a[this.sortKey];
        const bVal = b[this.sortKey];

        if (typeof aVal === 'boolean') {
          return order > 0 ? Number(aVal) - Number(bVal) : Number(bVal) - Number(aVal);
        }

        if (typeof aVal === 'string') {
          return order > 0 ? aVal.localeCompare(bVal) : bVal.localeCompare(aVal);
        }

        return order > 0 ? aVal - bVal : bVal - aVal;
      });
    },
    activeTaskCount() {
      return this.todos.filter(todo => !todo.complete).length;
    },
    completedTaskCount() {
      return this.todos.filter(todo => todo.complete).length;
    },
    Todos() {
      // ÂÖºÂÆπÊóß‰ª£Á†Å‰ΩøÁî®ÁöÑproperty
      return this.filteredTodos;
    },
    styleForSelectedRow() {
      return { background: 'rgba(33, 150, 243, 0.1)' };
    },
    styleForNonSelectedEvenRow() {
      return { background: 'transparent' };
    },
    styleForNonSelectedOddRow() {
      return { background: 'rgba(0, 0, 0, 0.03)' };
    }
  },
  mounted() {
    // Â∞ùËØï‰ªéÊú¨Âú∞Â≠òÂÇ®‰∏≠ÊÅ¢Â§çÊï∞ÊçÆ
    const savedTodos = localStorage.getItem('todos');
    const savedTheme = localStorage.getItem('isDarkMode');

    if (savedTodos) {
      try {
        this.todos = JSON.parse(savedTodos);
        // ËÆæÁΩÆ‰∏ã‰∏Ä‰∏™ID
        if (this.todos.length > 0) {
          const maxId = Math.max(...this.todos.map(t => t.id));
          this.nextTodoId = maxId + 1;
        }
      } catch (e) {
        console.error('Error parsing saved todos:', e);
        this.todos = [];
      }
    }

    if (savedTheme !== null) {
      this.isDarkMode = savedTheme === 'true';
    } else {
      // Ê£ÄÊµãÁ≥ªÁªüÂÅèÂ•Ω
      const prefersDark = window.matchMedia('(prefers-color-scheme: dark)').matches;
      this.isDarkMode = prefersDark;
    }

    // Ê∑ªÂä†ÈîÆÁõòÂø´Êç∑ÈîÆ
    window.addEventListener('keydown', this.handleKeydown);
  },
  beforeDestroy() {
    window.removeEventListener('keydown', this.handleKeydown);
  },
  watch: {
    todos: {
      handler(newTodos) {
        // ‰øùÂ≠òÂà∞Êú¨Âú∞Â≠òÂÇ®
        localStorage.setItem('todos', JSON.stringify(newTodos));
      },
      deep: true
    },
    isDarkMode(newValue) {
      localStorage.setItem('isDarkMode', newValue);
    }
  },
  methods: {
    addTodo() {
      if (!this.todo.trim()) return;

      this.todos.push({
        id: this.nextTodoId++,
        tag: this.tag.trim(),
        todo: this.todo.trim(),
        complete: false
      });

      this.todo = '';
      this.tag = '';
      this.$nextTick(() => {
        this.$refs.todoInput.focus();
      });
    },
    focusTodoInput() {
      this.$refs.todoInput.focus();
    },
    completed(todo) {
      // ‰ªªÂä°Áä∂ÊÄÅÊîπÂèòÊó∂Ëß¶ÂèëÁöÑÂ§ÑÁêÜ
      // ÂèØ‰ª•Âú®ËøôÈáåÊ∑ªÂä†‰øùÂ≠òÂà∞ÂêéÁ´ØÁöÑÈÄªËæë
    },
    deleteTodo(todo) {
      const index = this.todos.findIndex(t => t.id === todo.id);
      if (index !== -1) {
        this.todos.splice(index, 1);
      }
    },
    sortBy(key) {
      if (this.sortKey === key) {
        this.sortOrders[key] = -this.sortOrders[key];
      } else {
        this.sortKey = key;
      }
    },
    edit(id) {
      const todo = this.todos.find(t => t.id === id);
      if (!todo) return;

      this.editingTask = { ...todo };
      this.isEditing = true;
      this.selectedId = id;

      this.$nextTick(() => {
        if (this.$refs.editTaskInput) {
          this.$refs.editTaskInput.focus();
        }
      });
    },
    saveEdit() {
      if (!this.editingTask.todo.trim()) {
        return;
      }

      const index = this.todos.findIndex(t => t.id === this.editingTask.id);
      if (index !== -1) {
        this.todos[index] = { ...this.editingTask };
      }

      this.cancelEdit();
    },
    cancelEdit() {
      this.isEditing = false;
      this.editingTask = { id: null, tag: '', todo: '', complete: false };
      this.selectedId = null;
    },
    setFilter(filter) {
      this.activeFilter = filter;
    },
    clearCompleted() {
      this.todos = this.todos.filter(todo => !todo.complete);
    },
    toggleTheme() {
      this.isDarkMode = !this.isDarkMode;
    },
    handleKeydown(event) {
      // Â§ÑÁêÜÈîÆÁõòÂø´Êç∑ÈîÆ
      if (event.key === 'Escape' && this.isEditing) {
        this.cancelEdit();
      } else if (event.key === 'Enter' && event.ctrlKey && this.isEditing) {
        this.saveEdit();
      }
    }
  }
}
</script>

